body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.widget__field {
  position: relative;
  margin-right: 4px;
}

.widget__field-placeholder {
  position: absolute;
  color: #FFFFFF;
  z-index: 2;
  left: 14px;
  font-family: "Open Sans";
  font-size: 11.85px;
}

.widget .widget__auto-complete-field {
  overflow: hidden;
  font-weight: normal;
  font-size: 1.6rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: all 0.2s ease;
  background: #41548A;
  border: 1px solid #666 !important;
  border-bottom: 1px solid #FFFFFF !important;
  border-radius: 3px;
  color: #FFFFFF;
  font-family: "Open Sans";
  letter-spacing: 0.15px;
  line-height: 24px;
  padding-top: 15px !important;
}

.widget .widget__auto-complete-field:focus {
  border: 1px solid #666 !important;
}

.widget .p-autocomplete-panel {
  margin-top: 5px;
  border-radius: 1px;
}

.widget {
  max-width: 1100px;
  margin: 10px auto;
  padding: 52px;
  border-radius: 12px;
  background-color: #4E65A6;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.14), 0 2px 1px -1px rgba(0,0,0,0.12), 0 1px 3px 0 rgba(0,0,0,0.2);
}
